/*Dropdown menut*/
.dropDown {
    background-color: #f7f7f7;
    z-index: 1;
    position: absolute;
    color: #36918E;
    
    border-radius:5px;
    font-size:20px;
    display:none;

}


#palvelut:hover #palvelutDropDown{
    display:block;
}
